*{
  margin:0;
  padding:0;
}
.App {
.join{
 
  width:350px;
  margin:0 auto;
  padding:30px;
  border:1px solid #c1c1c1;
  border-radius: 3px;
  box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.3);;
  margin-top: 60px;
  &__wrapper {
    width: 350px;
  }
  &__title {
    text-align: center;
  }
  &__input-wrapper {
    padding: 0px 2rem;
  }
  &__sub-title {
    line-height: 1.2;
    margin: 20px 0;
  }
  &__logo {
    margin-top: -30px;
    width: 65px;
    img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }
  &__btn-container {
    display: inline-block;
    text-align: center;
  }

}
.u-input-style{
  padding: 8px 12px;
  margin:0 auto;
  margin-bottom: 16px;
  border: 1px solid #c5c6c9;
  border-radius: 4px;
  &:focus{
      outline: none;
  border-color: #95979d;
  

  }
}
.user-list{
  margin-top: 60px;
  .table{
    margin:0 auto;
    td, th {
      border: 1px solid #dddddd;
      text-align: left;
      padding: 8px 12px;
    }
    
    tr:nth-child(even) {
      background-color: #eee;
    }
  }

}
.t-sub-header{
  color: #2a2960;
  font-size: 3rem;
  font-family:sans-serif;
  margin-bottom: 40px;
  text-align: center;
}
.u-form{
  display: flex;
  flex-direction:column;
}
.u-submit-btn{

  color:white;

  font-family: monts-medium,sans-serif;
  padding: 10px 15px;
  background-color: #F66D70 ;
  border-radius: 3px;
  &:active{
      border:none;
  }
  
}

}
